import React from 'react'
import Highlight from 'react-highlight'

const moviedesc = () => {
  return (
    <div className="list">
                        <div className="item__inner">
                            <p className="desc">
                                On Movie Vue.js         .<br/>
                                <br/>
                                1.  ,  ,  ,     <br/>
                                     , ,    .<br/>
                                2.   <br/>
                                         .    , , ,     .<br/> 
                                3.   <br/>
                                       .<br/>
                            </p>
                        </div>
                        <div className="item__inner">
                            <p className="desc">
                                on Mounted<br />
                                onMounted Vue.js     . <br />   
                                Vue.js       ,        . <br />   
                                onMounted  Vue 3  ,        . <br />   
                            </p>
                        </div>
                        <div className="item__inner">
                            <p className="desc">
                                v-for ()<br/>
                                v-for          . <br/> 
                                v-if ()<br/>
                                v-if          . <br/>
                                <Highlight className="javascript"> 
                                {`<div class="credit cast">
        <div v-for="(cast, index) in movieCredits.cast.slice(0, 5)" :key="index">
            <img v-if="cast.profile_path" :src="'https://image.tmdb.org/t/p/w500' + cast.profile_path" :alt="cast.name">
            <img v-else src="../../assets/img/profile.jpg" alt="">
            <p class="character">{{ cast.character }}</p>
            <p class="name">{{ cast.name }}</p>
        </div>
  </div>`}
                                </Highlight>
                            </p>
                        </div>
                        <div className="item__inner">
                            <p className="desc">
                                API <br/>
                                <Highlight className="javascript"> 
                                {`const movies = ref([]);
onMounted(async () => {
    try {
        const response = await axios.get('https://api.themoviedb.org/3/movie/popular?language=en-US&page=1', {
        params: {
            api_key: ''//  API  
        },
        });

        console.log(response); // response.data    .
        movies.value = response.data.results;
        console.log(movies)
    } catch (err) {
        console.error('Error fetching popular movies:', err);
    }
});`}
                                </Highlight><br/>
                                  API <br/> 
                                <Highlight className="javascript"> 
                                {`const searchKeyword = ref(''); //   .
const searchMovies = async () => {
    try {
        const response = await axios.get('https://api.themoviedb.org/3/search/movie', {
        params: {
            api_key: '',//  API  
            language: 'KO-KR',
            page: '1',
            query: searchKeyword.value, // API .
        },
        });
        movies.value = response.data.results;
    } catch (err) {
        console.error(err);
    }
}

// input v-model 
<input type="search" v-model="searchKeyword" placeholder=" " @keyup.enter="searchMovies">
<button type="submit" @click="searchMovies"></button>`}

                                </Highlight>
                            </p>
                        </div>
                       
                    </div>
  )
}

export default moviedesc