import React from 'react'
import Highlight from 'react-highlight'

const Youtubedesc = () => {
  return (
    <div className="list">
                        <div className="item__inner">
                            <p className="desc">
                            Soccer Youtube<br/>
                             API            .<br />
                            <br/>
                             <br/>
                            1.  : 'Home', 'Today', 'Youtubers'  <br/>
                            2. data.js    :    data.js  ,   <br/>
                            3. Swiper  API : Swiper    UI , api.js   YouTube API  <br/>
                            4.     API :   ,  API    <br/>
                            5.    UI :   ,            UI <br/>
                            </p>
                        </div>
                        <div className="item__inner">
                            <p className="desc">
                            React Suspense<br/>
                            React Suspense        <br/>
                             React.lazy  ,          <br/>
                            React.Suspense           <br/>
                            <Highlight className="javascript"> 
                            {`import React, { lazy, Suspense } from 'react';
const MyComponent = lazy(() => import('./MyComponent'));

function App() {
    return (
        <div>
            <Suspense fallback={<div>Loading...</div>}>
                <MyComponent />
            </Suspense>
        </div>
    );
}`}
                            </Highlight><br/>
                            React.lazy<br/>
                            React.lazy      <br/>
                            React.lazy   import()  , import() Promise ,<br/> 
                             Promise     <br/>
                            , React.lazy        <br/>
                               <br/>
                            <Highlight className="javascript">
                            {`const MyComponent = React.lazy(() => import('./MyComponent'));`}
                            </Highlight><br/>
                            React.lazy React.Suspense            <br/>


                            </p>
                        </div>
                        
                    </div>
  )
}

export default Youtubedesc